// global styles
body {
  background-color: $white;
  color: $black;
  margin: 0;
  padding: 0;
}
.radius-16 {
  border-radius: 16px;
}
//margins
.mb-34 {
  margin-bottom: 34px;
}
.mt-34 {
  margin-top: 34px;
}
.mb-13 {
  margin-bottom: 13px;
}
.mb-55 {
  margin-bottom: 55px;
}
.mb-135 {
  margin-bottom: 135px;
}
.mt-144 {
  margin-top: 89px;
  @media #{$breakpoint-xl-ex} {
    margin-top: 144px;
  }
}

// button styles
.bt-blue {
  color: $white;
  background-color: $component-color2;
  transition: 0.3s;
  &:hover {
    // background-color: darken($component-color2,3%);
    background-color: $component-color2-darken;
    text-decoration: none;
    color: $white;
  }
}
.bt-gray {
  color: $black;
  background-color: $component-color4;
  transition: 0.3s;
  &:hover {
    background-color: darken($component-color4, 3%);
    text-decoration: none;
    color: $black;
  }
}

.page {
  &__content {
    padding-top: 100px;
    &-col {
      max-width: 666px;
      @media #{$breakpoint-lg} {
        max-width: 762px;
      }
      @media #{$breakpoint-xl-ex} {
        max-width: 1082px;
      }
      &-md {
        max-width: 666px;
        @media #{$breakpoint-md} {
          max-width: 762px;
        }
        @media #{$breakpoint-lg} {
          max-width: 918px;
        }
        @media #{$breakpoint-xl} {
          max-width: 960px;
        }
        @media #{$breakpoint-xl-ex} {
          max-width: 1082px;
        }
      }
    }
  }
  // page navigation
  &__nav {
    &__list {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      margin-top: 20px;
      white-space: nowrap;
      overflow-x: auto;
      &__item {
        list-style: none;
        margin-right: 10px;
        &-link {
          font-family: $firagoBold;
          font-size: 12px;
          color: $black;
          &:hover {
            color: $black;
          }
        }
        .icon {
          width: 4px;
          height: 7px;
          fill: black;
        }
      }
    }
  }
}

// search bar
.search {
  &__container {
    top: 0;
    left: 0;
    width: 100%;
    background: $white;
    min-height: 100vh;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 0.3s;
    transform: translateY(-100%);
    box-shadow: 0px 0px 30px #2026311a;
    position: fixed;
    height: 100%;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 8px;
      &-track {
        box-shadow: inset 0 0 5px grey;
        border-radius: 10px;
      }
      &-thumb {
        background: $component-color2;
        border-radius: 10px;
        &:hover {
          background: $component-color2;
        }
      }
    }

    &.active {
      transform: translateY(0);
    }
    &.result__active {
      top: 60px;
      .search__container__content {
        position: absolute;
        top: 60px;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
      }
    }
    &__row {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 21px;
    }
    &__title {
      font-size: 16px;
      font-family: $firagoBold;
      color: $black;
      margin-bottom: 0;
      @media #{$breakpoint-md} {
        font-size: 21px;
      }
    }
    &__form {
      width: 100%;
      position: relative;
      &__input {
        background-color: $component-color4;
        width: 100%;
        font-family: $firagoMedium;
        font-size: 16px;
        color: $black;
        border-radius: 8px;
        height: 60px;
        padding: 0 30px;
        &::-webkit-input-placeholder {
          color: rgba($color: $black, $alpha: 0.3);
        }
        &::-moz-placeholder {
          color: rgba($color: $black, $alpha: 0.3);
        }
        &:-ms-input-placeholder {
          color: rgba($color: $black, $alpha: 0.3);
        }
        &:-moz-placeholder {
          color: rgba($color: $black, $alpha: 0.3);
        }
        &:focus::placeholder {
          color: transparent;
        }
        &:focus {
          outline: none;
        }
      }
      .icon {
        width: 20px;
        height: 20px;
        stroke: $component-color2;
        position: absolute;
        cursor: pointer;
        right: 30px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  &__close {
    background-color: $component-color2;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    cursor: pointer;
    .icon {
      width: 20px;
      height: 20px;
      fill: $white;
    }
  }
  // search results
  &__result {
    margin-top: 55px;
    margin-bottom: 180px;
    display: none;
    &.active {
      display: block;
    }
    &__title {
      font-family: $firagoRegular;
      font-size: 21px;
      color: $black;
      @media #{$breakpoint-xl-ex} {
        font-size: 34px;
      }
      &.margins {
        margin-bottom: 34px;
        &.mt {
          margin-top: 45px;
        }
      }
      .purple {
        color: $component-color2;
        font-family: $firagoBold;
      }
    }
    &__word {
      color: $component-color2;
    }
  }
}

// successful registration
.registration {
  &__success {
    // min-height: calc(100vh - 100px - 240px);
    min-height: 45vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    max-width: 804px;
    margin: 50px auto;
    @media #{$breakpoint-md} {
      min-height: 65vh;
      margin: auto;
    }
    @media #{$breakpoint-xl-ex} {
      min-height: calc(100vh - 100px - 286px);
    }
    &.sm {
      max-width: 300px;
      width: 100%;
      margin: 50px auto;
      @media #{$breakpoint-md} {
        margin: auto;
      }
    }
    &__icon {
      // margin-bottom: 55px;
      width: 40px;
      height: 40px;
    }
    &__text {
      font-size: 21px;
      font-family: $firagoBold;
      color: $black;
      margin: 34px auto 50px;
      @media #{$breakpoint-lg} {
        margin: 55px auto 50px;
      }
    }
  }
}

// terms and privacy pages
.document {
  &__section {
    width: 100%;
    margin: auto;
  }
  &__title {
    color: $black;
    font-size: 21px;
    font-family: $firagoBold;
    margin-bottom: 34px;
    margin-top: 21px;
    @media #{$breakpoint-sm} {
      font-size: 34px;
    }
    @media #{$breakpoint-lg} {
      text-align: center;
    }
  }
  &__text {
    font-family: $firagoRegular;
    font-size: 16px;
    margin-bottom: 0;
    color: rgba($color: $black, $alpha: 0.6);
  }
  &__line {
    width: 100%;
    margin: 55px 0 85px;
    border-top: 2px dashed rgba($color: $component-color2, $alpha: 0.3);
    &.mg-sm {
      margin: 21px 0 34px;
    }
  }
  &__btn {
    &__container {
      margin-bottom: 55px;
      display: flex;
      justify-content: center;
    }
    padding: 10px;
    width: 100%;
    max-width: 290px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $white;
    box-shadow: 0 0 30px #2026310d;
    color: $black;
    font-family: $firagoBold;
    font-size: 16px;
    line-height: 1.2;
    transition: 0.3s;
    @media #{$breakpoint-md} {
      min-width: 387px;
      max-width: auto;
      width: auto;
    }
    &:hover {
      text-decoration: none;
      color: $black;
      box-shadow: none;
    }
  }
}

// pagination
.paging {
  &__container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 20px 0 50px;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: auto;
    padding-bottom: 2px;
    @media #{$breakpoint-lg} {
      &::-webkit-scrollbar {
        width: 100%;
        height: 2px;
        &-track {
          box-shadow: inset 0 0 5px $component-color3;
          background-color: $component-color3;
          border-radius: 10px;
        }
        &-thumb {
          background: $component-color2;
          border-radius: 10px;
          &:hover {
            background: $component-color2;
          }
        }
      }
    }
    @media #{$breakpoint-xl} {
      white-space: normal;
      overflow-x: visible;
      overflow-y: visible;
    }
    &.sm{
      margin: 0 0 34px;
      @media #{$breakpoint-md} {
        margin: 0 0 89px;
      }
      .paging__item{
        font-size: 16px;
        font-family: $firagoBold;
      }
    }
  }
  &__item {
    color: $black;
    font-size: 21px;
    font-family: $firagoMedium;
    width: 30px;
    height: 30px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    flex: none;
    &:last-child {
      margin-right: 0;
    }
    &:hover{
      color: $component-color2;
    }
    &.active {
      border: 2px solid $component-color2;
      color: $component-color2;
      flex: none;
      &:hover{
        color: $component-color2-darken;
        border: 2px solid $component-color2-darken;
      }
    }
    &.green{
      color: $component-color9;
      &:hover{
        color: $component-color2;
      }
    }
    &.bt-blue{
      flex: none;
      &:first-child{
        margin-right: 20px;
      }
      &:last-child{
        margin-left: 15px;
      }
    }
    .icon {
      width: 5px;
      fill: $white;
      &.arrow-left {
        transform: rotate(180deg);
      }
    }
  }
}

// home page
.section {
  &__main {
    width: 100%;
    display: flex;
    align-items: center;
    &__bg{
      background-color: $component-color1;
    }
    &.dark {
      background-color: $component-color1;
    }
    &.light {
      background-color: $white;
      @media #{$breakpoint-md} {
        max-width: 98%;
        margin: 0 auto;
        border-radius: 30px;
      }
    }
    &__title {
      font-family: $firagoBold;
      font-size: 21px;
      color: $black;
      margin-top: 34px;
      margin-bottom: 13px;
      @media #{$breakpoint-md} {
        font-size: 34px;
        margin-top: 55px;
        margin-bottom: 34px;
      }
      @media #{$breakpoint-xl} {
        font-size: 55px;
      }
      &.mt {
        margin-top: 34px;
        margin-bottom: 0;
        @media #{$breakpoint-md} {
          margin-top: 55px;
        }
        @media #{$breakpoint-xl} {
          margin-top: 89px;
        }
        @media #{$breakpoint-xl-ex} {
          margin-top: 144px;
        }
      }
      &.mb {
        margin-bottom: 34px;
        @media #{$breakpoint-md} {
          margin-bottom: 55px;
        }
      }
      &.mt-sm {
        margin-top: 21px;
        margin-bottom: 0;
        @media #{$breakpoint-md} {
          margin-top: 55px;
        }
      }
      &.mt-lg {
        @media #{$breakpoint-xl} {
          margin-top: 116px;
        }
      }
      &.mt-xl {
        @media #{$breakpoint-lg} {
          margin-top: 168px;
        }
        @media #{$breakpoint-xl} {
          margin-top: 138px;
        }
        @media #{$breakpoint-xxl} {
          margin-top: 232px;
        }
      }
    }
    &__text {
      font-family: $firagoRegular;
      font-size: 16px;
      color: rgba($color: $black, $alpha: 0.6);
      margin-bottom: 0;
      &.mt {
        margin-top: 21px;
        @media #{$breakpoint-md} {
          margin-top: 34px;
        }
      }
      &.mb {
        margin-bottom: 34px;
        @media #{$breakpoint-md} {
          margin-bottom: 55px;
        }
      }
      &-link {
        color: $component-color2;
        text-decoration: underline;
      }
    }
  }
  &__one {
    &__btn {
      min-width: 190px;
      height: 80px;
      border-radius: 16px;
      background-color: $white;
      box-shadow: 0px 0px 30px #2026310d;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $black;
      font-size: 16px;
      font-family: $firagoBold;
      transition: 0.3s;
      border: 2px solid transparent;
      padding: 20px;
      @media #{$breakpoint-md} {
        width: 100%;
      }
      @media #{$breakpoint-lg} {
        width: auto;
      }
      &:first-child {
        margin-bottom: 20px;
        @media #{$breakpoint-md} {
          margin-right: 30px;
          margin-bottom: 0;
        }
      }
      &__container {
        display: flex;
        flex-direction: column;
        margin-top: 34px;
        margin-bottom: 34px;
        @media #{$breakpoint-md} {
          flex-direction: row;
          margin-top: 55px;
          margin-bottom: 70px;
        }
        @media #{$breakpoint-xl} {
          margin-top: 89px;
        }
        &.mb-xl {
          @media #{$breakpoint-lg} {
            margin-bottom: 168px;
          }
          @media #{$breakpoint-xl} {
            margin-bottom: 138px;
          }
          @media #{$breakpoint-xxl} {
            margin-bottom: 232px;
          }
        }
      }
      &.green {
        .section__one__btn__icon-bg {
          background-color: $component-color9-light;
        }
        &:hover {
          border: 2px solid $component-color9;
          color: $black;
        }
      }
      &.yellow {
        .section__one__btn__icon-bg {
          background-color: $component-color7-light;
        }
        &:hover {
          border: 2px solid $component-color7;
          color: $black;
        }
      }
      &__icon {
        &-bg {
          width: 40px;
          height: 40px;
          border-radius: 8px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 20px;
        }
      }
    }
    &__img {
      width: 100%;
      object-fit: contain;
      margin-bottom: 34px;
      @media #{$breakpoint-lg} {
        margin-bottom: 0;
        max-height: 427px;
      }
      @media #{$breakpoint-xl-ex} {
        max-height: 610px;
      }
    }
  }
  &__two {
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__btn {
      border-radius: 8px;
      padding: 8px 42px 10px;
      display: inline-block;
      width: 100%;
      text-align: center;
      @media #{$breakpoint-sm} {
        width: auto;
      }
      &.mt {
        margin-top: 34px;
      }
      &.mb {
        margin-bottom: 34px;
        @media #{$breakpoint-md} {
          margin-bottom: 55px;
        }
        @media #{$breakpoint-lg} {
          margin-bottom: 89px;
        }
        @media #{$breakpoint-xxl} {
          margin-bottom: 135px;
        }
      }
    }
  }
  &__three {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__four {
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__item {
      margin-bottom: 55px;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      @media #{$breakpoint-lg} {
        margin-bottom: 0;
      }
      &__img {
        height: 248px;
        object-fit: contain;
        margin: auto;
      }
    }
    &__btn {
      margin-right: 0;
      margin-bottom: 0;
      &__container {
        display: flex;
        justify-content: center;
        margin-bottom: 34px;
        @media #{$breakpoint-md} {
          margin-bottom: 55px;
        }
        @media #{$breakpoint-lg} {
          margin-top: 55px;
        }
        @media #{$breakpoint-xl} {
          margin-bottom: 89px;
        }
        @media #{$breakpoint-xxl} {
          margin-bottom: 135px;
        }
      }
    }
  }
  &__five {
    &__content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      height: 100%;
    }
    &__img {
      width: 100%;
      object-fit: contain;
      margin-bottom: 34px;
      @media #{$breakpoint-lg} {
        margin-bottom: 0;
        max-height: 429px;
      }
      @media #{$breakpoint-xl-ex} {
        max-height: 610px;
      }
    }
  }
  &__six {
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__row {
      display: flex;
      flex-direction: column;
      margin: 34px 0;
      @media #{$breakpoint-md} {
        flex-direction: row;
        justify-content: space-evenly;
      }
      @media #{$breakpoint-lg} {
        margin: 55px 0;
      }
      @media #{$breakpoint-xl} {
        margin: 89px 0;
      }
      @media #{$breakpoint-xl-ex} {
        margin: 115px 0;
      }
      &__item {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
        &:last-child {
          margin-bottom: 0;
        }
        @media #{$breakpoint-md} {
          margin-bottom: 0;
        }
        &__img {
          height: 67px;
          object-fit: contain;
          @media #{$breakpoint-lg} {
            height: 80px;
          }
          &.mr {
            margin-right: 25px;
            @media #{$breakpoint-lg} {
              margin-right: 30px;
            }
          }
        }
      }
    }
  }
}
